@import url(https://fonts.googleapis.com/css?family=Raleway:400,200);
body {
  margin: 0;
  font-family:  -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 2s linear;
            animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



































































/* Background layer with an image */
.backdrop {
  background-color: transparent;
  /* background: url("https://res.cloudinary.com/css-tricks/image/upload/f_auto,q_auto/v1559739084/0619_White_OnlineForms_2_600x500_zfqkk2.jpg") center; */
}
.backdrop2 {
  background: transparent center;
  color: transparent !important;
  -webkit-background-clip: text;
}
/* Dark foreground layer, with white text, set to "multiply" mix blend mode */
.text {
  font-size: 5rem;
  color: white;
  background: rgb(59, 2, 6);
  mix-blend-mode: multiply;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.float-right{
  position: absolute;
  right: 0px;
  bottom: 10vw;
  z-index: 20000000;
}















































